.contactForm {
  display: flex;
  flex-direction: column;
  max-width: 270px;
  margin: 0 auto;
}

.labelItem {
  display: inline-flex;
  flex-direction: column;
  margin-bottom: 20px;
  font-size: 19px;
  font-weight: 600;
  color: #59719c;
}

input {
  display: block;
  margin-top: 5px;
  padding: 3px 8px;
  height: 30px;
  font-size: 17px;
  color: #445169;
  border: 1px solid #4f6080;
  border-radius: 6px;
}

.btn {
  font-size: 17px;
  font-weight: 600;
  width: 151px;
  height: 34px;
  margin: auto;
  color: #4f6080;
  background-color: #dce6f7;
  border-radius: 10px;
  border: 1px solid #dce6f7;
  box-shadow: -5px -5px 5px -5px rgb(34 60 80 / 60%) inset;
  cursor: pointer;
  transition: background-color, color 0.4s;
}

.btn:hover {
  background-color: #5a88d6;
  color: #fff;
}
